<template>
  <div>
    <div class="selectWrp">
      <span>本月领取量折线图</span>
    </div>
    <div id="container"></div>
    <div class="table3view">
      <div ref="cavwrp">
        <h4>本年度领取量</h4>
        <div id="table1"></div>
      </div>
      <div>
        <h4>月度领取量</h4>
        <div id="table2"></div>
      </div>
      <div>
        <h4>佩戴量</h4>
        <div id="table3"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { Line, Column } from "@antv/g2plot";
export default {
  mounted() {
    let cavouter = this.$refs.cavwrp;
    console.log(cavouter.clientWidth);
    let owidth = cavouter.clientWidth-20;
    this.drawsLine();
    this.drawTb1("table1", owidth);
    this.drawTb1("table2", owidth);
    this.drawTb1("table3", owidth);
  },
  methods: {
    drawTb1(target, width) {
      const data = [
        {
          month: "01",
          sales: 138
        },
        {
          month: "02",
          sales: 532
        },
        {
          month: "03",
          sales: 161
        },
        {
          month: "04",
          sales: 345
        },
        {
          month: "05",
          sales: 483
        },
        {
          month: "06",
          sales: 138
        },
        {
          month: "07",
          sales: 368
        },
        {
          month: "08",
          sales: 338
        },
        {
          month: "09",
          sales: 383
        },
        {
          month: "10",
          sales: 358
        },
        {
          month: "11",
          sales: 348
        },
        {
          month: "12",
          sales: 383
        }
      ];

      const columnPlot = new Column(document.getElementById(target), {
        width: width,
        title: {
          visible: false,
          text: "基础柱状图-图形标签"
        },
        description: {
          visible: false,
          text: "基础柱状图图形标签默认位置在柱形上部。"
        },
        forceFit: false,
        xAxis: {
          title: {
            visible: false
          }
        },
        yAxis: {
          title: {
            visible: false
          }
        },

        data,
        padding: "auto",
        xField: "month",
        yField: "sales",
        label: {
          visible: true,
          style: {
            fill: "#0D0E68",
            fontSize: 12,
            fontWeight: 600,
            opacity: 0.6
          }
        }
      });

      columnPlot.render();
    },
    drawsLine() {
      const data = [
        { year: "2020年1月1日", value: 123 },
        { year: "2020年1月3日", value: 234 },
        { year: "2020年1月7日", value: 433 },
        { year: "2020年1月11日", value: 245 },
        { year: "2020年1月13日", value: 439 },
        { year: "2020年1月15日", value: 622 },
        { year: "2020年1月20日", value: 357 },
        { year: "2020年1月25日", value: 559 },
        { year: "2020年1月30日", value: 633 }
      ];

      const linePlot = new Line(document.getElementById("container"), {
        title: {
          visible: true,
          alignTo: "right",
          text: "设备佩戴数"
        },
        description: {
          visible: false,
          alignTo: "right",
          text: "2020年对应月份不同日期设备佩戴数。"
        },
        forceFit: true,
        padding: "auto",
        data,
        xField: "year",
        yField: "value",
        point: {
          visible: true
        },
        label: {
          visible: true,
          type: "point"
        }
      });

      linePlot.render();
    }
  }
};
</script>

<style scoped>
.selectWrp {
  display: flex;
  justify-content: flex-start;
}
.selectWrp span {
  margin-right: 12px;
}
.table3view {
  display: flex;
  justify-content: space-between;
}
.table3view h4 {
  text-align: left;
}
.table3view > div {
  padding: 10px;
  box-sizing: border-box;
  width: 31%;
}
</style>